import { useState } from "react";
import { useMutation, useQueryClient, useQuery } from "@tanstack/react-query";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Loader2, GitBranch as WorkflowIcon, FileText, Bot, UserCheck } from "lucide-react";
import { toast } from "sonner";
import { useNavigate } from "react-router-dom";
import {
  createWorkflow,
  getWorkflowTemplates,
} from "@/api/services/workflows/workflowService";
import { VapiWorkflowService } from "@/api/services/vapi/vapiService";
import { fetchAllAgentsComplete } from "@/api/services/agents/agentService";
import { WorkflowTemplate } from "@/types/api";
import CreditGuard from "@/components/Credits/CreditGuard";

const formSchema = z.object({
  name: z.string().min(1, "Name is required").max(80, "Name must be 80 characters or less"),
  description: z.string().optional(),
  template: z.string().optional(),
  assistantId: z.string().optional(),
});

type FormData = z.infer<typeof formSchema>;

const CreateWorkflow = () => {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const [selectedTemplate, setSelectedTemplate] = useState<WorkflowTemplate | null>(null);

  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: "",
      description: "",
      template: "",
    },
  });

  // Fetch workflow templates
  const { data: templatesData, isLoading: templatesLoading } = useQuery({
    queryKey: ["workflow-templates"],
    queryFn: getWorkflowTemplates,
  });

  // Fetch available agents
  const { data: agentsData, isLoading: agentsLoading } = useQuery({
    queryKey: ["agents-all"],
    queryFn: () => fetchAllAgentsComplete(""),
  });

  // Create workflow mutation - Use VAPI service for proper VAPI integration
  const createMutation = useMutation({
    mutationFn: VapiWorkflowService.createWorkflow,
    onSuccess: (data) => {
      console.log("VAPI Workflow creation response:", data);
      queryClient.invalidateQueries({ queryKey: ["workflows"] });
      toast.success("Workflow created successfully");
      // Navigate to workflow builder
      const workflowId = data?.id;
      console.log("Navigating to workflow:", workflowId);
      navigate(`/workflows/${workflowId}/edit`);
    },
    onError: (error: any) => {
      console.error("VAPI Workflow creation error:", error);
      toast.error(error.response?.data?.message || "Failed to create workflow");
    },
  });

  const onSubmit = (data: FormData) => {
    let workflowData: any = {
      name: data.name,
      description: data.description,
      assistantId: data.assistantId === "none" ? undefined : data.assistantId, // Handle "none" value
    };

    // If template is selected, use template data
    if (selectedTemplate) {
      workflowData = {
        ...workflowData,
        nodes: selectedTemplate.nodes,
        edges: selectedTemplate.edges,
      };
    } else {
      // Create basic workflow with start node
      workflowData = {
        ...workflowData,
        nodes: [
          {
            type: "conversation",
            name: "start",
            isStart: true,
            prompt: "You are a helpful AI assistant. Greet the user and ask how you can help them today."
          }
        ],
        edges: [],
      };
    }

    createMutation.mutate(workflowData);
  };

  const handleTemplateSelect = (templateId: string) => {
    const template = templatesData?.data?.find((t: WorkflowTemplate) => t.id === templateId);
    setSelectedTemplate(template || null);
    form.setValue("template", templateId);
    
    if (template) {
      // Auto-fill name if empty
      if (!form.getValues("name")) {
        form.setValue("name", template.name);
      }
      // Auto-fill description if empty
      if (!form.getValues("description")) {
        form.setValue("description", template.description);
      }
    }
  };

  const templates = templatesData?.data || [];

  return (
    <CreditGuard
      requiredCredits={1}
      feature="workflow creation"
      showUpgradeButton={true}
    >
      <div className="space-y-6 h-full overflow-y-auto p-1">
      <div className="flex items-center gap-3 pb-4 border-b border-gray-200">
        <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
          <WorkflowIcon className="h-4 w-4 text-blue-600" />
        </div>
        <h2 className="text-xl font-semibold text-gray-900">Create New Workflow</h2>
      </div>

      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
          {/* Basic Information */}
          <Card className="border border-gray-200 shadow-sm">
            <CardHeader className="pb-4">
              <CardTitle className="text-lg font-medium text-gray-900">Basic Information</CardTitle>
              <CardDescription className="text-gray-600">
                Provide basic details for your workflow
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4 pt-0">
              <FormField
                control={form.control}
                name="name"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Name *</FormLabel>
                    <FormControl>
                      <Input placeholder="Enter workflow name" {...field} />
                    </FormControl>
                    <FormDescription>
                      A descriptive name for your workflow (max 80 characters)
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="description"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Description</FormLabel>
                    <FormControl>
                      <Textarea
                        placeholder="Describe what this workflow does..."
                        className="resize-none"
                        rows={3}
                        {...field}
                      />
                    </FormControl>
                    <FormDescription>
                      Optional description to help you remember the workflow's purpose
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="assistantId"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel className="flex items-center gap-2">
                      <Bot className="h-4 w-4 text-primary" />
                      Select Agent (Optional)
                    </FormLabel>
                    <Select onValueChange={field.onChange} value={field.value}>
                      <FormControl>
                        <SelectTrigger>
                          <SelectValue placeholder="Choose an agent for this workflow" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent>
                        <SelectItem value="none">No Agent (Create from workflow)</SelectItem>
                        {agentsLoading ? (
                          <SelectItem value="loading" disabled>
                            <Loader2 className="h-4 w-4 animate-spin mr-2" />
                            Loading agents...
                          </SelectItem>
                        ) : (
                          agentsData?.data?.map((agent: any) => (
                            <SelectItem key={agent.id} value={agent.id}>
                              <div className="flex items-center gap-2">
                                <UserCheck className="h-4 w-4 text-green-600" />
                                {agent.name}
                              </div>
                            </SelectItem>
                          ))
                        )}
                      </SelectContent>
                    </Select>
                    <FormDescription>
                      Select an existing agent to use with this workflow, or leave empty to create one automatically
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </CardContent>
          </Card>

          {/* Template Selection */}
          <Card className="border border-gray-200 shadow-sm">
            <CardHeader className="pb-4">
              <CardTitle className="flex items-center gap-2 text-lg font-medium text-gray-900">
                <FileText className="h-4 w-4 text-gray-600" />
                Choose Template
              </CardTitle>
              <CardDescription className="text-gray-600">
                Start with a pre-built template or create from scratch
              </CardDescription>
            </CardHeader>
            <CardContent className="pt-0">
              {templatesLoading ? (
                <div className="flex items-center justify-center py-8">
                  <Loader2 className="h-6 w-6 animate-spin" />
                  <span className="ml-2">Loading templates...</span>
                </div>
              ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {/* Blank Template */}
                  <Card
                    className={`cursor-pointer transition-all duration-200 border ${
                      !selectedTemplate
                        ? "ring-2 ring-blue-500 bg-blue-50 border-blue-200"
                        : "hover:bg-gray-50 hover:border-gray-300 border-gray-200"
                    }`}
                    onClick={() => {
                      setSelectedTemplate(null);
                      form.setValue("template", "");
                    }}
                  >
                    <CardContent className="p-4">
                      <div className="flex items-center gap-3">
                        <div className="w-10 h-10 bg-gray-100 rounded-lg flex items-center justify-center">
                          <WorkflowIcon className="h-5 w-5 text-gray-600" />
                        </div>
                        <div className="flex-1">
                          <h3 className="font-medium">Blank Workflow</h3>
                          <p className="text-sm text-gray-600">Start from scratch</p>
                        </div>
                      </div>
                    </CardContent>
                  </Card>

                  {/* Template Options */}
                  {templates.map((template: WorkflowTemplate) => (
                    <Card
                      key={template.id}
                      className={`cursor-pointer transition-all duration-200 border ${
                        selectedTemplate?.id === template.id
                          ? "ring-2 ring-blue-500 bg-blue-50 border-blue-200"
                          : "hover:bg-gray-50 hover:border-gray-300 border-gray-200"
                      }`}
                      onClick={() => handleTemplateSelect(template.id)}
                    >
                      <CardContent className="p-4">
                        <div className="flex items-center gap-3">
                          <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center">
                            <FileText className="h-5 w-5 text-blue-600" />
                          </div>
                          <div className="flex-1">
                            <div className="flex items-center gap-2">
                              <h3 className="font-medium">{template.name}</h3>
                              <Badge variant="outline" className="text-xs">
                                {template.category}
                              </Badge>
                            </div>
                            <p className="text-sm text-gray-600 mt-1">
                              {template.description}
                            </p>
                            <div className="flex items-center gap-2 mt-2">
                              <Badge variant="secondary" className="text-xs">
                                {template.nodes?.length || 0} nodes
                              </Badge>
                            </div>
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>

          {/* Submit Button */}
          <div className="flex justify-end gap-3 pt-4 border-t border-gray-200">
            <Button
              type="button"
              variant="outline"
              onClick={() => form.reset()}
              className="px-6"
            >
              Reset
            </Button>
            <Button
              type="submit"
              disabled={createMutation.isPending}
              className="min-w-[140px] px-6 bg-blue-600 hover:bg-blue-700"
            >
              {createMutation.isPending ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Creating...
                </>
              ) : (
                "Create Workflow"
              )}
            </Button>
          </div>
        </form>
      </Form>
    </div>
    </CreditGuard>
  );
};

export default CreateWorkflow;
