import { useCallback, useRef } from 'react';

/**
 * Custom hook to handle accessible dropdown menu interactions
 * Prevents aria-hidden focus conflicts and improves keyboard navigation
 */
export function useAccessibleDropdown() {
  const triggerRef = useRef<HTMLButtonElement>(null);

  const handleOpenAutoFocus = useCallback((event: Event) => {
    // Prevent the default auto-focus behavior that can conflict with aria-hidden
    event.preventDefault();
    
    // Allow natural focus flow instead of forcing focus
    // This prevents the aria-hidden warning while maintaining accessibility
  }, []);

  const handleCloseAutoFocus = useCallback((event: Event) => {
    // Prevent focus issues when closing dropdown
    event.preventDefault();
    
    // Return focus to the trigger button manually for better UX
    if (triggerRef.current) {
      triggerRef.current.focus();
    }
  }, []);

  const handleKeyDown = useCallback((event: React.KeyboardEvent) => {
    // Handle escape key to close dropdown
    if (event.key === 'Escape') {
      event.preventDefault();
      if (triggerRef.current) {
        triggerRef.current.focus();
      }
    }
  }, []);

  return {
    triggerRef,
    handleOpenAutoFocus,
    handleCloseAutoFocus,
    handleKeyDown,
    // Props to spread on DropdownMenuContent
    contentProps: {
      onOpenAutoFocus: handleOpenAutoFocus,
      onCloseAutoFocus: handleCloseAutoFocus,
      onKeyDown: handleKeyDown,
    },
    // Props to spread on DropdownMenuTrigger
    triggerProps: {
      ref: triggerRef,
    },
  };
}

/**
 * Utility function to create accessible dropdown menu item props
 */
export function createAccessibleMenuItem(
  label: string,
  action: string = 'activate'
) {
  return {
    'aria-label': `${action} ${label}`,
    role: 'menuitem',
  };
}

/**
 * Utility function to create accessible dropdown trigger props
 */
export function createAccessibleTrigger(itemName: string) {
  return {
    'aria-label': `Actions for ${itemName}`,
    'aria-haspopup': 'menu' as const,
    'aria-expanded': false, // This will be managed by Radix
  };
}
